.page-nav{font-size:20px;list-style:none;display:flex;justify-content:center;align-items:center;margin-top:10px;margin-bottom:10px;width:fit-content}.page-span{display:inline-block;margin:0 10px}.page-span.jump{font-size:18px}.page-link.active{color:rgba(15,133,243,0.938);text-decoration:underline}.prev,.next,.first,.last{display:inline-flex;justify-content:center;align-items:center}.prev .page-link,.next .page-link,.first .page-link,.last .page-link{display:inline-flex;justify-content:center;align-items:center}a.page-link{text-decoration:none;color:black}a.page-link:hover{color:rgba(120,190,255,0.938);transition:none}a.page-link svg{width:20px;height:20px;fill:black}a.page-link svg:hover{fill:rgba(120,190,255,0.938)}a.page-link.disable svg{fill:gray}.nav-container{opacity:0;position:absolute;bottom:2px;display:flex;justify-content:center;width:90%;height:43px;background-color:whitesmoke;transition:all 0.1s}.nav-container:hover{box-shadow:0px -4px 6px 4px whitesmoke;opacity:1}.jump-input{width:40px;height:30px;border:none;border-bottom:1px solid black;background-color:transparent;margin:0 10px;text-align:center;font-size:15px}.jump-input:focus{outline:none}